<template>
  <div class="home">
    <transition name="slide-fade">
      <Header
        v-if="show"
        @on-scrollIntoView="scrollIntoView"
        v-b-scrollspy:nav-scroller
        :fixed="fixed"
        :background="background"
        :color="color"
      ></Header>
    </transition>

    <div id="nav-scroller" ref="content">
      <index />
      <information />
      <experience />
      <skills />
      <products />
      <contact />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import index from "@/views/index.vue";
import Header from "@/components/Header";
export default {
  name: "Home",
  data() {
    return {
      show: false,
      fixed: "top",
      background: "rgba(255,255,255,0.9)",
      color: "#000",
    };
  },
  components: {
    Header,
    index,
    information: () => import("@/views/information"),
    experience: () => import("@/views/experience"),
    skills: () => import("@/views/skills"),
    products: () => import("@/views/products"),
    contact: () => import("@/views/contact"),
  },
  methods: {
    // Convenience method to scroll a heading into view.
    // Not required for scrollspy to work
    scrollIntoView(evt) {
      // evt.preventDefault();
      const href = evt.target.getAttribute("href");
      const el = href ? document.querySelector(href) : null;
      console.log(el);
      if (el) {
        this.$refs.content.scrollTop = el.offsetTop;
      }
    },
    showNav() {
      window.addEventListener("scroll", () => {
        if (window.scrollY > 56) {
          this.show = true;
        } else {
          this.show = false;
        }
      });
    },
  },
  created() {
    this.showNav();
  },
};
</script>

<style lang="scss">
.home {
  overflow: hidden;
}
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  // transform: translateY(-56px);
  opacity: 0;
}
</style>
