import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
// import VueAxios from 'vue-axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    partTitle:'主标题',
    haveSubTitle: true,
    subTitle: '副标题',
    textAlign: 'center',
    information: {},
    login: false
  },
  mutations: {
    changeLogin(state){
      state.login = !state.login
    }
  },
  actions: {
    getInfo(content){
      axios.get('/data/info.json').then(res => {
        content.state.information = res.data
      })
    }
  },
  modules: {
  }
})
